<template>
  <div id='over' class="card-container" :style="{backgroundImage: 'url(' + logo + ')'}">
    <h1>Thanks, Bye!</h1>
  </div>
</template>

<script>
  import Logo from '@/assets/logo.png'
  export default {
    name: 'AuctionLots',
    data () {
      return {
        logo: Logo
      }
    },
    methods: {
    },
    components: {
    },
    mounted: function () {
    }
  }
</script>

<style scoped>
  #over {
    display: table;
    width: 100%;
    height:300px;
    margin: 0 auto;
    background-repeat:no-repeat;
    background-size: 110px;
    background-position: 48% 10%;
  }
  #over h1{
    display: table-cell;
    vertical-align:middle;
    color: #323232
  }
</style>



